@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;500;600&display=swap);
html {
  font-size: calc(0.2rem + 1vw);
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto Mono', monospace, 'Roboto', -apple-system,
    BlinkMacSystemFont, 'Segoe UI', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background: #050a30; */
  background: #131522;
  color: #f4f6fc;
}

a,
a:visited {
  color: inherit;
  text-decoration: none;
}

h1 {
  text-align: center;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

.cm-s-material-ocean.CodeMirror {
  /* background-color: #050a30; */
  height: 100% !important;
  font-size: 0.8rem;
}

.code-mirror-wrapper {
  flex-grow: 1;
  overflow: hidden;
  margin: 0em 0.1em 0.2em 0.1em;
  min-height: 23vh;
}

.form-info {
  position: absolute;
  right: -1.5em;
  bottom: 0.7em;
}

